import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { DatePicker } from "@mui/x-date-pickers/DatePicker";
import { useState } from "react";
import { v4 as uuidv4 } from "uuid";
import dayjs from "dayjs";

const schema = z.object({
  name: z
    .string()
    .min(1, { message: "Description should be at least 1 character." })
    .max(50),
  expire: z
    .number({ invalid_type_error: "Amount is required" })
    .min(1)
    .max(100_100),
});

interface Props {
  onSubmit: (data: SumType) => void;
}

type ExpenseFormData = z.infer<typeof schema>;

type ProduceDate = {
  produce: string;
};

type UUID = {
  uuid: string;
};

type SumType = ExpenseFormData & ProduceDate & UUID;

const ExpenseForm = ({ onSubmit }: Props) => {
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm<SumType>({ resolver: zodResolver(schema) });

  const [produceDate, setProduceDate] = useState(new Date());

  const onChange = (value: Date) => {
    setProduceDate(() => new Date(value));
  };

  return (
    <form
      onSubmit={handleSubmit((data) => {
        data.uuid = uuidv4().toString();
        data.produce = `${
          produceDate.getMonth() + 1
        }/${produceDate.getDate()}/${produceDate.getFullYear()}`;
        onSubmit(data);
        reset();
      })}
    >
      <div className="mb-3">
        <label htmlFor="name" className="form-label">
          
        </label>
        <input
          {...register("name")}
          id="name"
          type="text"
          className="form-control"
        />
      </div>
      {errors.name && <p className="text-danger">{errors.name.message}</p>}
      <div className="mb-3">
        :
        <DatePicker
          value={dayjs(Date.now())}
          onChange={(val) => {
            if (val != null) {
              onChange;
            }
          }}
        />
        {errors.produce && (
          <p className="text-danger">{errors.produce.message}</p>
        )}
      </div>
      <div className="mb-3">
        <label htmlFor="expire" className="form-label">
          
        </label>
        <input
          {...register("expire", { valueAsNumber: true })}
          id="expire"
          type="number"
          className="form-control"
        />
        {errors.expire && (
          <p className="text-danger">{errors.expire.message}</p>
        )}
      </div>
      <button className="btn btn-primary"></button>
    </form>
  );
};

export default ExpenseForm;
