import { Navbar, Container, Nav } from "react-bootstrap";
import { Link } from "react-router-dom";

const NavBar = () => {
  return (
    <Navbar bg="primary" data-bs-theme="dark">
      <Container>
        <Navbar.Brand></Navbar.Brand>
        <Nav className="me-auto">
          <Nav.Link>
            <Link to="/"></Link>
          </Nav.Link>
          <Nav.Link>
            <Link to="/form"></Link>
          </Nav.Link>
        </Nav>
      </Container>
    </Navbar>
  );
};

export default NavBar;
